import { Suspense, lazy, useEffect, useState } from "react";
import Layout from "components/Layout";
import { getProductsNew } from "system/axios/api/product";
import SkeletonLoading from "loading/SkeletonLoading";

//     
const REQUEST_COUNT = 20;

const ProductList = lazy(() => import("components/ProductList"));

const HomePage = () => {
  const [viewProducts, setViewProducts] = useState();
  const [totalCount, setTotalCount] = useState(0);
  const [cursor, setCursor] = useState(0);

  useEffect(() => {
    handleGetProduct();
  }, []);

  //   (loadMore :    true)
  const handleGetProduct = async () => {
    const params = {
      count: REQUEST_COUNT,
      cursor: cursor,
    };

    //         
    if (!cursor) {
      await (() => {
        return new Promise((resolve) => {
          setTimeout(() => {
            resolve();
          }, 1000);
        });
      })();
    }

    getProductsNew(params).then((res) => {
      const { list, totalCount } = res.data.result;
      setViewProducts((prev = []) => [...prev, ...list]);

      setTotalCount(totalCount);
      setCursor(cursor + REQUEST_COUNT);
    });
  };

  return (
    <Layout>
      <Suspense fallback={<SkeletonLoading />}>
        <PromiseWrapper isLoading={!viewProducts}>
          <ProductList
            viewProducts={viewProducts}
            hasMore={totalCount >= cursor}
            next={() => {
              handleGetProduct();
            }}
            cursor={cursor}
          />
        </PromiseWrapper>
      </Suspense>
    </Layout>
  );
};
export default HomePage;

/** Loading Promise throw suspense fallback   Wrapper Component */
const PromiseWrapper = (props) => {
  const { isLoading } = props;

  if (isLoading) throw new Promise(() => {});

  return { ...props.children };
};
